body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: lightsteelblue;
  color: white;
  font-size: 20px;
}
span{
  font-size: 16px;
}
form{
  width: 300px;
  margin: 100px auto 0px auto;
}
form *{
  display: block;
}
input[type="text"] {
  height: 30px;
  width: 100%;
  border-radius: 10px;
  border: 0px;
  margin-bottom: 15px;
  font-size: 16px;
}
input[type="text"]:focus {
  outline: 0px;
  height: 30px;
  width: 100%;
  border-radius: 10px;
  border: 0px;
  margin-bottom: 15px;
  font-size: 16px;
}
::placeholder{
  color: black;
  font-size: 16px;
}
button {
  background-color: dodgerblue;
  border: none;
  color: #fff;
  cursor: pointer;
  height: 35px;
  width: 100px;
  outline: none;
  border-radius: 10px;
  margin: auto;
}
.error{
  color: red;
  width: fit-content;
  margin: 10px auto;
}
.show{
  width: fit-content;
  margin: 20px auto;
}
.card{
  width: fit-content;
  text-align: center;
  margin: 30px auto;
  padding: 30px 100px;
  border-radius: 15px;
  border: 2px solid white;
}
.delete{
  margin-right: 10px;
}
.formEdit{
  margin: 30px auto 0px auto;
}
.clear{
  width: 200px;
  font-size: 18px;
  height: 40px;
  margin-top: 30px;
}
